import { TopParticipantsComponent } from './top-participants/top-participants.component';
import { JudgementComponent } from './judgement.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JudgeListComponent } from '../judge-list/judge-list.component';



const routes: Routes = [
  
  {
    path: '',
    component: JudgementComponent,      
  },
  {
    path: 'top-participants',
    component: TopParticipantsComponent,      
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class JudgementRoutingModule { }
